import React, { useState } from 'react';
import { Container, Typography, Grid, Card, CardContent, Stack, Fade, Grow, Box, Dialog, DialogTitle, DialogContent, DialogActions, Button, CardMedia, CardActionArea, IconButton } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import { API_BASE } from '../api/client';

const featuresData = [
  {
    t: 'Role-based Panels',
    d: 'Student, Teacher, and Admin dashboards tailored to needs.',
    img: 'https://images.unsplash.com/photo-1460925895917-afdab827c52f?auto=format&fit=crop&w=600&q=80',
    fullDesc: 'Our system identifies the user role and presents a customized dashboard. Students see grades and attendance, Teachers manage classes, and Admins oversee the entire institution.'
  },
  {
    t: 'Admissions',
    d: 'Tests, document uploads, and automated evaluation.',
    img: 'https://images.unsplash.com/photo-1554224155-8d04cb21cd6c?auto=format&fit=crop&w=600&q=80',
    fullDesc: 'Streamline the admission process with online forms, document verification, and automated entrance test scheduling and result processing.'
  },
  {
    t: 'Attendance & Grades',
    d: 'Track progress and performance with ease.',
    img: 'https://images.unsplash.com/photo-1584697964405-b9e713d07416?auto=format&fit=crop&w=600&q=80',
    fullDesc: 'Real-time attendance tracking and comprehensive gradebook management. Parents get instant updates on their child\'s academic progress.'
  },
  {
    t: 'Announcements',
    d: 'Notify classes and publish updates instantly.',
    img: 'https://images.unsplash.com/photo-1576670159805-381a38034c53?auto=format&fit=crop&w=600&q=80',
    fullDesc: 'A powerful notice board system to broadcast messages to specific classes, entire streams, or the whole school instantly.'
  },
  {
    t: 'Results & Gallery',
    d: 'Showcase achievements and campus life.',
    img: 'https://images.unsplash.com/photo-1523240795612-9a054b0db644?auto=format&fit=crop&w=600&q=80',
    fullDesc: 'Celebrate student success with digital report cards and a vibrant photo gallery capturing the spirit of our school events.'
  },
  {
    t: 'Careers',
    d: 'Post and apply for teaching positions.',
    img: 'https://images.unsplash.com/photo-1521791136064-7986c2920216?auto=format&fit=crop&w=600&q=80',
    fullDesc: 'Join our team! A dedicated portal for aspiring teachers to apply for open positions and track their application status.'
  },
];

const Features = () => {
  const [open, setOpen] = useState(false);
  const [selectedFeature, setSelectedFeature] = useState(null);

  const handleOpen = (feature) => {
    setSelectedFeature(feature);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setTimeout(() => setSelectedFeature(null), 200);
  };

  return (
    <>
      <Box sx={{
        position: 'relative',
        color: 'common.white',
        py: { xs: 12, md: 20 },
        minHeight: '60vh',
        display: 'flex',
        alignItems: 'center',
        backgroundImage: `linear-gradient(to right, rgba(15, 23, 42, 0.9), rgba(15, 23, 42, 0.6)), url(${API_BASE.replace('/api', '') + '/site-images/college.jpg'})`, // Changed to college.jpg for variety
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundAttachment: 'fixed'
      }}>
        <Container>
          <Fade in timeout={700}>
            <Stack spacing={2} alignItems="flex-start" textAlign="left">
              <Typography variant="overline" sx={{ fontWeight: 700, letterSpacing: 2, color: 'secondary.light' }}>
                Why Choice Us
              </Typography>
              <Typography variant="h1" sx={{
                fontSize: { xs: '3rem', md: '5rem' },
                fontWeight: 800,
                textShadow: '0 4px 20px rgba(0,0,0,0.5)'
              }}>
                World Class <br /> Facilities
              </Typography>
              <Typography variant="h5" sx={{ maxWidth: 600, color: 'rgba(255,255,255,0.9)', fontWeight: 400, mt: 2 }}>
                Everything you need to run a modern educational institution, integrated into one seamless platform.
              </Typography>
            </Stack>
          </Fade>
        </Container>
      </Box>

      <Container sx={{ py: { xs: 8, md: 12 } }}>
        <Grid container spacing={4}>
          {featuresData.map((f, i) => (
            <Grid item xs={12} sm={6} md={4} key={f.t}>
              <Grow in timeout={500 + (i % 3) * 150}>
                <Card sx={{
                  height: '100%',
                  display: 'flex',
                  flexDirection: 'column',
                  transition: '0.3s',
                  '&:hover': {
                    transform: 'translateY(-8px)',
                    boxShadow: '0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04)'
                  }
                }}>
                  <CardActionArea onClick={() => handleOpen(f)} sx={{ flexGrow: 1, display: 'flex', flexDirection: 'column', alignItems: 'flex-start' }}>
                    <CardMedia
                      component="img"
                      height="200"
                      image={f.img}
                      alt={f.t}
                    />
                    <CardContent sx={{ flexGrow: 1 }}>
                      <Typography variant="h6" gutterBottom sx={{ fontWeight: 700 }}>{f.t}</Typography>
                      <Typography color="text.secondary" variant="body2">{f.d}</Typography>
                    </CardContent>
                  </CardActionArea>
                </Card>
              </Grow>
            </Grid>
          ))}
        </Grid>
      </Container>

      {/* Feature Details Modal */}
      <Dialog
        open={open}
        onClose={handleClose}
        maxWidth="md"
        fullWidth
        TransitionComponent={Grow}
        PaperProps={{ sx: { borderRadius: 3, overflow: 'hidden' } }}
      >
        {selectedFeature && (
          <>
            <Box sx={{ position: 'relative', height: 250, bgcolor: 'grey.200' }}>
              <CardMedia
                component="img"
                height="100%"
                image={selectedFeature.img}
                alt={selectedFeature.t}
                sx={{ objectFit: 'cover' }}
              />
              <IconButton
                onClick={handleClose}
                sx={{ position: 'absolute', top: 16, right: 16, bgcolor: 'rgba(255,255,255,0.8)', '&:hover': { bgcolor: 'white' } }}
              >
                <CloseIcon />
              </IconButton>
            </Box>
            <DialogContent sx={{ p: 4 }}>
              <Typography variant="h4" gutterBottom sx={{ fontWeight: 700 }}>{selectedFeature.t}</Typography>
              <Typography variant="body1" paragraph color="text.secondary" sx={{ fontSize: '1.1rem', lineHeight: 1.6 }}>
                {selectedFeature.fullDesc}
              </Typography>

              <Box sx={{ mt: 3, p: 3, bgcolor: 'primary.50', borderRadius: 2, border: '1px solid', borderColor: 'primary.100' }}>
                <Typography variant="subtitle2" color="primary.main" sx={{ fontWeight: 700 }}>Why this matters</Typography>
                <Typography variant="body2" color="text.primary">
                  Efficient management of {selectedFeature.t.toLowerCase()} is crucial for a smooth school operation. Our system automates this to save time and reduce errors.
                </Typography>
              </Box>
            </DialogContent>
            <DialogActions sx={{ p: 3, pt: 0 }}>
              <Button onClick={handleClose} variant="outlined" size="large">Close</Button>
            </DialogActions>
          </>
        )}
      </Dialog>
    </>
  );
};

export default Features;
