import React, { useEffect, useState } from 'react';
import { Container, Typography, Paper, Stack, Grid, Card, CardContent, Chip } from '@mui/material';
import api from '../../api/client';

const daysOrder = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri'];

const TeacherSchedule = () => {
  const [entries, setEntries] = useState([]);
  const [teacher, setTeacher] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    (async () => {
      setLoading(true);
      try {
        const { data } = await api.get('/teacher/schedule');
        setEntries(Array.isArray(data.entries) ? data.entries : []);
        setTeacher(data.teacher || null);
        setError('');
      } catch (e) {
        setError(e?.response?.data?.message || 'Failed to load schedule');
      } finally {
        setLoading(false);
      }
    })();
  }, []);

  const grouped = daysOrder.map(d => ({ day: d, items: entries.filter(e => e.day === d) }));

  return (
    <Container sx={{ py: { xs: 6, md: 8 } }}>
      <Stack spacing={1} sx={{ mb: 2 }}>
        <Typography variant="h4">My Schedule</Typography>
        {teacher && <Typography variant="subtitle1" color="text.secondary">{teacher.name}  {teacher.subject || 'No subject'}</Typography>}
      </Stack>
      {loading ? (
        <Typography>Loading...</Typography>
      ) : error ? (
        <Typography color="error">{error}</Typography>
      ) : (
        <Grid container spacing={2}>
          {grouped.map(g => (
            <Grid item xs={12} md={6} lg={4} key={g.day}>
              <Paper sx={{ p: 2 }}>
                <Stack spacing={1}>
                  <Typography variant="h6">{g.day}</Typography>
                  {g.items.length === 0 ? (
                    <Typography color="text.secondary">No class</Typography>
                  ) : (
                    g.items.map((it, idx) => (
                      <Card key={idx}>
                        <CardContent>
                          <Stack direction="row" spacing={1} alignItems="center">
                            <Typography variant="subtitle1">Class {it.classNumber}</Typography>
                            <Chip size="small" color="secondary" label={it.subject} />
                            <Chip size="small" label={`Period ${it.period}`} />
                            {(it.startTime && it.endTime) ? (
                              <Chip size="small" label={`${it.startTime} - ${it.endTime}`} />
                            ) : null}
                          </Stack>
                        </CardContent>
                      </Card>
                    ))
                  )}
                </Stack>
              </Paper>
            </Grid>
          ))}
        </Grid>
      )}
    </Container>
  );
};

export default TeacherSchedule;
