import React, { useEffect, useState } from 'react';
import { Container, Typography, Paper, Stack, TextField, Button, Grid, Card, CardContent } from '@mui/material';
import api from '../../api/client';

const TeacherAssignments = () => {
  const [subject, setSubject] = useState('');
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [dueDate, setDueDate] = useState('');
  const [loading, setLoading] = useState(false);
  const [list, setList] = useState([]);
  const [error, setError] = useState('');

  const load = async () => {
    try {
      const { data } = await api.get('/teacher/assignments');
      setList(Array.isArray(data) ? data : []);
    } catch (e) {
      setError(e?.response?.data?.message || 'Failed to load assignments');
    }
  };

  useEffect(() => { load(); }, []);

  const submit = async () => {
    if (!subject || !title) return alert('Subject and Title are required');
    setLoading(true);
    try {
      await api.post('/teacher/assignments', { subject, title, description, dueDate: dueDate || undefined });
      setSubject('');
      setTitle('');
      setDescription('');
      setDueDate('');
      await load();
      alert('Assignment created');
    } catch (e) {
      alert(e?.response?.data?.message || 'Failed to create assignment');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container sx={{ py: { xs: 6, md: 8 } }}>
      <Typography variant="h4" sx={{ mb: 2 }}>Assignments</Typography>
      <Grid container spacing={3}>
        <Grid item xs={12} md={5}>
          <Paper sx={{ p: 2 }}>
            <Stack spacing={1}>
              <TextField size="small" label="Subject" value={subject} onChange={(e)=>setSubject(e.target.value)} placeholder="e.g., Mathematics" />
              <TextField size="small" label="Title" value={title} onChange={(e)=>setTitle(e.target.value)} />
              <TextField size="small" label="Description" value={description} onChange={(e)=>setDescription(e.target.value)} multiline rows={3} />
              <TextField size="small" type="date" label="Due Date" InputLabelProps={{ shrink: true }} value={dueDate} onChange={(e)=>setDueDate(e.target.value)} />
              <Button variant="contained" onClick={submit} disabled={loading}>Create Assignment</Button>
              {error ? <Typography color="error" variant="body2">{error}</Typography> : null}
            </Stack>
          </Paper>
        </Grid>
        <Grid item xs={12} md={7}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="h6" sx={{ mb: 1 }}>My Assignments</Typography>
            {list.length === 0 ? (
              <Typography color="text.secondary">No assignments yet</Typography>
            ) : (
              <Stack spacing={1}>
                {list.map((a) => (
                  <Card key={a._id}>
                    <CardContent>
                      <Typography variant="subtitle1">{a.title}</Typography>
                      <Typography variant="body2" color="text.secondary">Subject: {a.subject}{a.dueDate ? `  Due: ${new Date(a.dueDate).toLocaleDateString()}` : ''}</Typography>
                      {a.description ? <Typography variant="body2" sx={{ mt: 1 }}>{a.description}</Typography> : null}
                    </CardContent>
                  </Card>
                ))}
              </Stack>
            )}
          </Paper>
        </Grid>
      </Grid>
    </Container>
  );
};

export default TeacherAssignments;
