import React, { useEffect, useState } from 'react';
import { Container, Typography, Grid, Paper, Stack, List, ListItem, ListItemText, TextField, Button, Divider, Chip } from '@mui/material';
import api from '../../api/client';

const StudentDashboard = () => {
  const [profile, setProfile] = useState(null);
  const [admission, setAdmission] = useState(null);
  const [loading, setLoading] = useState(true);
  const [assignments, setAssignments] = useState([]);
  const [submissions, setSubmissions] = useState([]);
  const [submissionContent, setSubmissionContent] = useState({}); // assignmentId -> content
  const [todayAttendance, setTodayAttendance] = useState(null);

  useEffect(() => {
    (async () => {
      try {
        const [{ data: me }, { data: adm }] = await Promise.all([
          api.get('/student/me'),
          api.get('/student/admission'),
        ]);
        setProfile(me);
        setAdmission(adm);
        if (me?.classNumber) {
          await Promise.all([loadAssignments(), loadSubmissions(), loadTodayAttendance()]);
        }
      } catch {}
      setLoading(false);
    })();
  }, []);

  const loadAssignments = async () => {
    const { data } = await api.get('/student/assignments');
    setAssignments(data || []);
  };

  const loadSubmissions = async () => {
    const { data } = await api.get('/student/submissions');
    setSubmissions(data || []);
  };

  const loadTodayAttendance = async () => {
    const today = new Date();
    const yyyy = today.getFullYear();
    const mm = String(today.getMonth() + 1).padStart(2, '0');
    const dd = String(today.getDate()).padStart(2, '0');
    const { data } = await api.get(`/student/attendance?date=${yyyy}-${mm}-${dd}`);
    setTodayAttendance(data && data[0] ? data[0] : null);
  };

  const handleSubmitAssignment = async (assignmentId) => {
    const content = submissionContent[assignmentId];
    if (!content) return;
    await api.post(`/student/assignments/${assignmentId}/submit`, { content });
    setSubmissionContent((m) => ({ ...m, [assignmentId]: '' }));
    await loadSubmissions();
  };

  const handleCheckIn = async () => {
    await api.post('/student/attendance/checkin');
    await loadTodayAttendance();
  };

  return (
    <Container sx={{ py: 6 }}>
      <Typography variant="h4" gutterBottom>Student Dashboard</Typography>
      {loading ? (
        <Typography>Loading...</Typography>
      ) : (
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <Paper sx={{ p: 2 }}>
              <Stack spacing={0.5}>
                <Typography variant="h6">Profile</Typography>
                <Typography>Name: {profile?.name}</Typography>
                <Typography>Email: {profile?.email}</Typography>
                <Typography>Student ID: {profile?.studentId || ''}</Typography>
              </Stack>
            </Paper>
          </Grid>
          <Grid item xs={12} md={6}>
            <Paper sx={{ p: 2 }}>
              <Stack spacing={0.5}>
                <Typography variant="h6">Class</Typography>
                <Typography>Class Number: {profile?.classNumber || ''}</Typography>
                <Divider sx={{ my: 1 }} />
                <Stack direction="row" spacing={1} alignItems="center">
                  <Typography variant="body2">Today's Attendance:</Typography>
                  {todayAttendance ? (
                    <Chip color={todayAttendance.present ? 'success' : 'warning'} label={todayAttendance.present ? 'Present' : 'Absent'} size="small" />
                  ) : (
                    <Chip color="default" label="Not Marked" size="small" />
                  )}
                  <Button size="small" variant="contained" onClick={handleCheckIn} disabled={!!todayAttendance?.present}>Check In</Button>
                </Stack>
              </Stack>
            </Paper>
          </Grid>
          <Grid item xs={12}>
            <Paper sx={{ p: 2 }}>
              <Stack spacing={0.5}>
                <Typography variant="h6">Admission Status</Typography>
                <Typography>Status: {admission?.status || ''}</Typography>
                {admission?.testScore != null && (
                  <Typography>Test Score: {admission.testScore}</Typography>
                )}
              </Stack>
            </Paper>
          </Grid>
          <Grid item xs={12} md={7}>
            <Paper sx={{ p: 2 }}>
              <Typography variant="h6" sx={{ mb: 1 }}>Assignments</Typography>
              {assignments.length === 0 ? (
                <Typography color="text.secondary">No assignments yet</Typography>
              ) : (
                <List>
                  {assignments.map((a) => (
                    <ListItem key={a._id} divider>
                      <ListItemText primary={`${a.subject}: ${a.title}`} secondary={`Due: ${a.dueDate ? new Date(a.dueDate).toLocaleDateString() : ''}`} />
                      <TextField
                        size="small"
                        placeholder="Submission link or text"
                        value={submissionContent[a._id] || ''}
                        onChange={(e)=>setSubmissionContent((m)=>({ ...m, [a._id]: e.target.value }))}
                        sx={{ mr: 1, width: 260 }}
                      />
                      <Button variant="contained" size="small" onClick={()=>handleSubmitAssignment(a._id)}>Submit</Button>
                    </ListItem>
                  ))}
                </List>
              )}
            </Paper>
          </Grid>
          <Grid item xs={12} md={5}>
            <Paper sx={{ p: 2 }}>
              <Typography variant="h6" sx={{ mb: 1 }}>My Submissions</Typography>
              {submissions.length === 0 ? (
                <Typography color="text.secondary">No submissions</Typography>
              ) : (
                <List>
                  {submissions.map((s) => (
                    <ListItem key={s._id} divider>
                      <ListItemText primary={s.assignment ? s.assignment.title : 'Assignment'} secondary={`Submitted: ${new Date(s.submittedAt).toLocaleString()}`} />
                    </ListItem>
                  ))}
                </List>
              )}
            </Paper>
          </Grid>
        </Grid>
      )}
    </Container>
  );
};

export default StudentDashboard;