import React, { useState } from 'react';
import {
  Container,
  Paper,
  Typography,
  TextField,
  Button,
  Box,
  Alert,
  Link
} from '@mui/material';
import { useNavigate, useLocation, useParams } from 'react-router-dom';
import { useAuth } from '../../context/AuthContext.jsx';

const API = 'http://localhost:5000/api';

const Login = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { role: routeRole } = useParams();
  const { login: setAuthUser } = useAuth();
  const [formData, setFormData] = useState({
    email: location.state?.email || '',
    password: '',
  });
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const normalizedRole = routeRole ? String(routeRole).toLowerCase() : null;

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');

    if (!formData.email || !formData.password) {
      setError('All fields are required');
      return;
    }

    setLoading(true);

    try {
      const res = await fetch(`${API}/auth/login`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email: formData.email, password: formData.password })
      });
      const data = await res.json();
      if (!res.ok) {
        setError('Invalid email or password');
        setLoading(false);
        return;
      }
      // If on a role-specific route, enforce matching role
      if (normalizedRole && data.role !== normalizedRole) {
        setError(`This page only allows ${normalizedRole} login. You attempted to log in as ${data.role}.`);
        setLoading(false);
        return;
      }
      // Persist token and role for authenticated routes
      localStorage.setItem('currentUser', JSON.stringify({
        token: data.token,
        role: data.role,
        verified: data.verified,
        classAssigned: data.classAssigned,
        studentId: data.studentId,
        email: formData.email,
      }));
      // Update context immediately so ProtectedRoute detects auth without a reload
      setAuthUser({
        token: data.token,
        role: data.role,
        verified: data.verified,
        classAssigned: data.classAssigned,
        studentId: data.studentId,
        email: formData.email,
      });
      if (data.role === 'admin') {
        navigate('/admin/dashboard');
      } else if (data.role === 'teacher') {
        navigate('/teacher/dashboard');
      } else if (data.role === 'student') {
        navigate('/student/dashboard');
      } else {
        navigate('/');
      }
    } catch (err) {
      setError('Failed to log in. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box sx={{ display: 'grid', placeItems: 'center', minHeight: 'calc(100vh - 140px)', py: 6 }}>
      <Container component="main" maxWidth="xs">
        <Box sx={{ animation: 'fadeIn 600ms ease' }}>
          <Paper elevation={3} sx={{ p: 3, borderRadius: 3 }}>
            <Typography component="h1" variant="h5" sx={{ fontWeight: 700, textAlign: 'center' }}>
              Bal Vikas Public School
            </Typography>
            <Typography component="h2" variant="h6" color="text.secondary" sx={{ textAlign: 'center', mt: 0.5 }}>
              {`Login as ${normalizedRole ? (normalizedRole.charAt(0).toUpperCase() + normalizedRole.slice(1)) : 'General User'}`}
            </Typography>
            {!normalizedRole && (
              <Box mt={2} mb={2} display="flex" flexDirection="column" gap={1}>
                <Button size="small" variant="outlined" sx={{ py: 0.5, fontSize: 14 }} onClick={() => navigate('/login/student')}>Login as Student</Button>
                <Button size="small" variant="outlined" sx={{ py: 0.5, fontSize: 14 }} onClick={() => navigate('/login/teacher')}>Login as Teacher</Button>
                <Button size="small" variant="outlined" sx={{ py: 0.5, fontSize: 14 }} onClick={() => navigate('/login/admin')}>Login as Admin</Button>
              </Box>
            )}
            {error && (
              <Alert severity="error" sx={{ mt: 2, mb: 2 }}>
                {error}
              </Alert>
            )}
            <Box component="form" onSubmit={handleSubmit}>
              <TextField
                margin="normal"
                required
                fullWidth
                label="Email Address"
                name="email"
                autoComplete="email"
                autoFocus
                value={formData.email}
                onChange={handleChange}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                name="password"
                label="Password"
                type="password"
                autoComplete="current-password"
                value={formData.password}
                onChange={handleChange}
              />
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                disabled={loading}
                sx={{ mt: 1.5 }}
              >
                {loading ? 'Logging in...' : 'Sign In'}
              </Button>
              <Box mt={2} textAlign="center">
                <Typography variant="body2" color="text.secondary">
                  Don't have an account?{' '}
                  <Link
                    component="button"
                    variant="body2"
                    onClick={() => navigate('/signup')}
                  >
                    Sign Up
                  </Link>
                </Typography>
              </Box>
            </Box>
          </Paper>
        </Box>
      </Container>
    </Box>
  );
};

export default Login;