import React, { useEffect, useMemo, useState } from 'react';
import {
  Container, Typography, Grid, Card, CardContent, CardActionArea, TextField, Stack, Chip, Dialog,
  DialogTitle, DialogContent, DialogActions, Button, Avatar, Box, InputAdornment, Divider,
  IconButton, Tooltip, LinearProgress, Skeleton
} from '@mui/material';
import {
  Search as SearchIcon,
  FilterList as FilterListIcon,
  PersonAdd as PersonAddIcon,
  Email as EmailIcon,
  Phone as PhoneIcon,
  Class as ClassIcon,
  Category as CategoryIcon,
  Verified as VerifiedIcon,
  Pending as PendingIcon,
  Close as CloseIcon,
  Refresh as RefreshIcon,
  Edit as EditIcon,
  School as SchoolIcon,
  Work as WorkIcon,
  AdminPanelSettings as AdminPanelSettingsIcon,
  LocationOn as LocationOnIcon,
  Event as EventIcon
} from '@mui/icons-material';
import { styled } from '@mui/material/styles';
import api from '../../api/client';

// Styled components
const StyledCard = styled(Card)(({ theme }) => ({
  height: '100%',
  display: 'flex',
  flexDirection: 'column',
  transition: 'all 0.3s ease',
  '&:hover': {
    transform: 'translateY(-4px)',
    boxShadow: theme.shadows[8],
  },
}));

const StatusChip = styled(Chip)(({ theme, status }) => ({
  fontWeight: 600,
  ...(status === 'verified' && {
    backgroundColor: theme.palette.success.light,
    color: theme.palette.success.dark,
  }),
  ...(status === 'pending' && {
    backgroundColor: theme.palette.warning.light,
    color: theme.palette.warning.dark,
  }),
}));

const DetailItem = ({ icon, label, value }) => (
  <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
    <Box sx={{ color: 'text.secondary', mr: 1 }}>{icon}</Box>
    <Typography variant="body2" color="text.secondary">
      <strong>{label}:</strong> {value || 'N/A'}
    </Typography>
  </Box>
);

const AdminTeachers = () => {
  const [teachers, setTeachers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [classFilter, setClassFilter] = useState('');
  const [selected, setSelected] = useState(null);
  const [tempPass, setTempPass] = useState('');
  const [isFilterOpen, setIsFilterOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    (async () => {
      try {
        const { data } = await api.get('/admin/teachers');
        setTeachers(Array.isArray(data) ? data : []);
      } catch {
        setTeachers([]);
      } finally {
        setLoading(false);
      }
    })();
  }, []);

  const filteredTeachers = useMemo(() => {
    const term = searchTerm.trim().toLowerCase();
    const cls = String(classFilter || '').trim();
    return teachers.filter(teacher => {
      const matchesSearch = !term || 
        (teacher.name && teacher.name.toLowerCase().includes(term)) ||
        (teacher.email && teacher.email.toLowerCase().includes(term)) ||
        (teacher.phone && teacher.phone.includes(term));
      const matchesClass = !cls || String(teacher.classAssigned || '') === cls;
      return matchesSearch && matchesClass;
    });
  }, [teachers, searchTerm, classFilter]);

  const handleRefresh = async () => {
    try {
      setIsLoading(true);
      const { data } = await api.get('/admin/teachers');
      setTeachers(Array.isArray(data) ? data : []);
    } catch (error) {
      console.error('Error refreshing teachers:', error);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Container maxWidth="xl" sx={{ 
      py: { xs: 3, md: 4 },
      px: { xs: 2, sm: 3, md: 4 },
      mx: 'auto',
      width: '100%'
    }}>
      <Box sx={{ 
        mb: 4,
        width: '100%',
        maxWidth: '100%',
        mx: 'auto',
        px: { xs: 0, sm: 2 }
      }}>
        <Stack 
          direction={{ xs: 'column', sm: 'row' }} 
          justifyContent="space-between" 
          alignItems={{ xs: 'flex-start', sm: 'center' }} 
          spacing={2}
          sx={{ width: '100%' }}
        >
          <Box sx={{ flex: 1, minWidth: 0 }}>
            <Typography variant="h4" component="h1" sx={{ 
              fontWeight: 700, 
              color: 'primary.main',
              fontSize: { xs: '1.75rem', sm: '2rem' },
              lineHeight: 1.2
            }}>
              Teacher Management
            </Typography>
            <Typography variant="body1" color="text.secondary" sx={{ mt: 0.5 }}>
              Manage all teachers, view details, and track their activities
            </Typography>
          </Box>
          <Button
            variant="contained"
            color="primary"
            startIcon={<PersonAddIcon />}
            onClick={() => { /* Handle add new teacher */ }}
            sx={{ 
              borderRadius: 2,
              width: { xs: '100%', sm: 'auto' },
              mt: { xs: 2, sm: 0 }
            }}
          >
            Add Teacher
          </Button>
        </Stack>
      </Box>

      <Card sx={{ 
        mb: 3, 
        p: { xs: 1.5, sm: 2 }, 
        borderRadius: 2, 
        boxShadow: '0 2px 8px rgba(0,0,0,0.05)',
        width: '100%',
        maxWidth: '100%',
        mx: 'auto',
        overflow: 'hidden'
      }}>
        <Grid container spacing={{ xs: 1.5, sm: 2 }} alignItems="center">
          <Grid item xs={12} sm={6} md={4}>
            <TextField
              fullWidth
              size="small"
              placeholder="Search teachers..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon color="action" fontSize="small" />
                  </InputAdornment>
                ),
                sx: { 
                  borderRadius: 2, 
                  bgcolor: 'background.paper',
                  fontSize: '0.875rem'
                }
              }}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={4} lg={3}>
            <TextField
              fullWidth
              select
              size="small"
              label="Filter by Class"
              value={classFilter}
              onChange={(e) => setClassFilter(e.target.value)}
              SelectProps={{ 
                native: true,
                style: { fontSize: '0.875rem' }
              }}
              sx={{ 
                bgcolor: 'background.paper', 
                borderRadius: 2,
                '& .MuiInputBase-input': {
                  fontSize: '0.875rem'
                }
              }}
            >
              <option value="">All Classes</option>
              {[...Array(12)].map((_, i) => (
                <option key={i + 1} value={i + 1}>
                  Class {i + 1}
                </option>
              ))}
            </TextField>
          </Grid>
          <Grid item xs={12} sm={12} md={4} lg={3} sx={{ 
            display: 'flex', 
            gap: 1,
            justifyContent: { xs: 'flex-start', sm: 'flex-end' },
            mt: { xs: 0, sm: 0 }
          }}>
            <Button
              variant="outlined"
              startIcon={<FilterListIcon />}
              onClick={() => setIsFilterOpen(!isFilterOpen)}
              sx={{ 
                borderRadius: 2, 
                flex: { xs: 1, sm: 'none' },
                minWidth: '120px',
                fontSize: '0.875rem'
              }}
            >
              Filters
            </Button>
            <Tooltip title="Refresh">
              <Button
                variant="outlined"
                onClick={handleRefresh}
                disabled={isLoading}
                sx={{ 
                  minWidth: '40px', 
                  width: '40px',
                  height: '40px',
                  p: 1,
                  '& .MuiSvgIcon-root': {
                    fontSize: '1.25rem'
                  }
                }}
              >
                <RefreshIcon />
              </Button>
            </Tooltip>
          </Grid>
        </Grid>
      </Card>

      {isLoading ? (
        <Box sx={{ width: '100%', mt: 2, mx: 'auto', maxWidth: '100%' }}>
          <LinearProgress />
        </Box>
      ) : filteredTeachers.length === 0 ? (
        <Box sx={{ 
          textAlign: 'center', 
          py: 8, 
          bgcolor: 'background.paper', 
          borderRadius: 2, 
          mt: 2,
          mx: 'auto',
          width: '100%',
          maxWidth: '100%',
          px: { xs: 2, sm: 3 }
        }}>
          <SearchIcon sx={{ fontSize: 60, color: 'text.disabled', mb: 2 }} />
          <Typography variant="h6" color="text.secondary" gutterBottom>
            No teachers found
          </Typography>
          <Typography variant="body2" color="text.secondary">
            Try adjusting your search or filter criteria
          </Typography>
        </Box>
      ) : (
        <Grid container spacing={{ xs: 2, md: 3 }} sx={{ mt: 0 }}>
          {filteredTeachers.map((teacher) => (
            <Grid item key={teacher._id} xs={12} sm={6} md={4} lg={3}>
              <StyledCard>
                <CardActionArea 
                  onClick={() => setSelected(teacher)}
                  sx={{ height: '100%', display: 'flex', flexDirection: 'column', alignItems: 'stretch' }}
                >
                  <CardContent sx={{ p: 3, flex: 1 }}>
                    <Stack direction="row" spacing={2} alignItems="center">
                      <Avatar 
                        sx={{ 
                          width: 56, 
                          height: 56, 
                          bgcolor: 'primary.main',
                          fontSize: '1.5rem',
                          fontWeight: 600
                        }}
                      >
                        {(teacher.name || '?').charAt(0).toUpperCase()}
                      </Avatar>
                      <Box sx={{ flex: 1, minWidth: 0 }}>
                        <Typography variant="subtitle1" fontWeight={600} noWrap sx={{ fontSize: { xs: '1rem', sm: '1.125rem' } }}>
                          {teacher.name || 'Unnamed Teacher'}
                        </Typography>
                        <Typography variant="body2" color="text.secondary" noWrap sx={{ fontSize: { xs: '0.875rem', sm: '1rem' } }}>
                          {teacher.email || 'No email'}
                        </Typography>
                      </Box>
                    </Stack>

                    <Stack direction="row" spacing={1} sx={{ mt: 2, flexWrap: 'wrap', gap: 0.5 }}>
                      {teacher.phone && (
                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                          <PhoneIcon color="action" fontSize="small" sx={{ mr: 0.5 }} />
                          <Typography variant="body2" color="text.secondary">
                            {teacher.phone}
                          </Typography>
                        </Box>
                      )}
                      {teacher.classAssigned && (
                        <Chip 
                          size="small" 
                          label={`Class ${teacher.classAssigned}`} 
                          color="primary"
                          variant="outlined"
                          sx={{ fontSize: '0.7rem', height: 24 }}
                        />
                      )}
                    </Stack>
                    
                    {(teacher.managementRole || (teacher.subjects && teacher.subjects.length > 0)) && (
                      <Box sx={{ mt: 2, display: 'flex', gap: 1, flexWrap: 'wrap' }}>
                        {teacher.managementRole && (
                          <Chip 
                            size="small" 
                            label={teacher.managementRole.includes('1-6') ? 'Admissions 1-6' : 'Admissions 6-12'}
                            color="secondary"
                            variant="outlined"
                            sx={{ fontSize: '0.7rem', height: 24 }}
                          />
                        )}
                        {teacher.subjects && teacher.subjects.map((subject, idx) => (
                          <Chip 
                            key={idx} 
                            size="small" 
                            label={subject}
                            variant="outlined"
                            sx={{ fontSize: '0.7rem', height: 24 }}
                          />
                        ))}
                      </Box>
                    )}
                  </CardContent>
                </CardActionArea>
              </StyledCard>
            </Grid>
          ))}
        </Grid>
      )}

      <Dialog 
        open={!!selected} 
        onClose={() => setSelected(null)} 
        maxWidth="sm" 
        fullWidth
        PaperProps={{
          sx: {
            borderRadius: 3,
            backgroundImage: 'linear-gradient(to bottom right, #f8fafc, #f0f7ff)'
          }
        }}
      >
        {selected && (
          <>
            <DialogTitle sx={{ 
              display: 'flex', 
              justifyContent: 'space-between', 
              alignItems: 'center',
              borderBottom: '1px solid',
              borderColor: 'divider',
              pb: 2,
              background: 'linear-gradient(90deg, #1a56db0a 0%, #7e3af20a 100%)'
            }}>
              <Box>
                <Typography variant="h6" fontWeight={600} color="primary">
                  Teacher Details
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  ID: {selected._id}
                </Typography>
              </Box>
              <IconButton onClick={() => setSelected(null)} size="small">
                <CloseIcon />
              </IconButton>
            </DialogTitle>
            
            <DialogContent dividers sx={{ p: 0 }}>
              <Box sx={{ p: 3, bgcolor: 'background.paper', borderRadius: 1, mb: 2, mx: 2, mt: 2 }}>
                <Stack direction="row" spacing={3} alignItems="center">
                  <Avatar 
                    sx={{ 
                      width: 80, 
                      height: 80, 
                      bgcolor: 'primary.main',
                      fontSize: '2rem',
                      fontWeight: 600
                    }}
                  >
                    {(selected.name || '?').charAt(0).toUpperCase()}
                  </Avatar>
                  <Box>
                    <Typography variant="h5" fontWeight={700} gutterBottom>
                      {selected.name}
                      <StatusChip 
                        label={selected.verified ? 'Verified' : 'Pending Verification'}
                        status={selected.verified ? 'verified' : 'pending'}
                        size="small"
                        sx={{ ml: 1, verticalAlign: 'middle' }}
                      />
                    </Typography>
                    
                    <Stack direction="row" spacing={2} flexWrap="wrap" sx={{ '& > *': { mb: 1 } }}>
                      <Chip 
                        icon={<EmailIcon fontSize="small" />} 
                        label={selected.email || 'No email'} 
                        size="small"
                        variant="outlined"
                      />
                      {selected.phone && (
                        <Chip 
                          icon={<PhoneIcon fontSize="small" />} 
                          label={selected.phone} 
                          size="small"
                          variant="outlined"
                        />
                      )}
                      {selected.classAssigned && (
                        <Chip 
                          icon={<ClassIcon fontSize="small" />} 
                          label={`Class ${selected.classAssigned}`} 
                          size="small"
                          color="primary"
                          variant="outlined"
                        />
                      )}
                    </Stack>
                  </Box>
                </Stack>
              </Box>
              
              <Box sx={{ p: 3 }}>
                <Typography variant="subtitle1" fontWeight={600} color="text.primary" gutterBottom>
                  Teacher Information
                </Typography>
                
                <Grid container spacing={2} sx={{ mb: 3 }}>
                  <Grid item xs={12} md={6}>
                    <DetailItem 
                      icon={<ClassIcon color="action" fontSize="small" />}
                      label="Assigned Class"
                      value={selected.classAssigned ? `Class ${selected.classAssigned}` : 'Not assigned'}
                    />
                    {selected.subjects && selected.subjects.length > 0 && (
                      <DetailItem 
                        icon={<CategoryIcon color="action" fontSize="small" />}
                        label="Subjects"
                        value={selected.subjects.join(', ')}
                      />
                    )}
                    {selected.qualification && (
                      <DetailItem 
                        icon={<SchoolIcon color="action" fontSize="small" />}
                        label="Qualification"
                        value={selected.qualification}
                      />
                    )}
                  </Grid>
                  <Grid item xs={12} md={6}>
                    {selected.joiningDate && (
                      <DetailItem 
                        icon={<EventIcon color="action" fontSize="small" />}
                        label="Joining Date"
                        value={new Date(selected.joiningDate).toLocaleDateString()}
                      />
                    )}
                    {selected.experience && (
                      <DetailItem 
                        icon={<WorkIcon color="action" fontSize="small" />}
                        label="Experience"
                        value={`${selected.experience} years`}
                      />
                    )}
                    {selected.managementRole && (
                      <DetailItem 
                        icon={<AdminPanelSettingsIcon color="action" fontSize="small" />}
                        label="Management Role"
                        value={selected.managementRole.includes('1-6') ? 'Admissions (Classes 1-6)' : 'Admissions (Classes 6-12)'}
                      />
                    )}
                  </Grid>
                </Grid>
                
                {selected.address && (
                  <Box sx={{ mb: 3 }}>
                    <Typography variant="subtitle2" color="text.secondary" gutterBottom>
                      <LocationOnIcon color="action" fontSize="small" sx={{ verticalAlign: 'middle', mr: 0.5 }} />
                      Address
                    </Typography>
                    <Typography variant="body2">
                      {selected.address}
                      {selected.city && `, ${selected.city}`}
                      {selected.state && `, ${selected.state}`}
                      {selected.pincode && ` - ${selected.pincode}`}
                    </Typography>
                  </Box>
                )}
                
                <Box sx={{ display: 'flex', gap: 2, mt: 3, pt: 2, borderTop: '1px solid', borderColor: 'divider' }}>
                  <Button
                    variant="contained"
                    color="primary"
                    startIcon={<EditIcon />}
                    onClick={() => { /* Handle edit */ }}
                    fullWidth
                  >
                    Edit Profile
                  </Button>
                  <Button
                    variant="outlined"
                    color="primary"
                    startIcon={<EmailIcon />}
                    onClick={() => { /* Handle email */ }}
                    fullWidth
                  >
                    Send Message
                  </Button>
                </Box>
              </Box>
            </DialogContent>
            <DialogActions sx={{ p: 2, borderTop: '1px solid', borderColor: 'divider' }}>
              <Button 
                onClick={() => setSelected(null)}
                variant="outlined"
                color="inherit"
                sx={{ borderRadius: 2 }}
              >
                Close
              </Button>
              <Button 
                variant="contained" 
                color="primary"
                onClick={async () => {
                  try {
                    const { data } = await api.post(`/admin/teacher/${selected._id}/reset-password`);
                    setTempPass(data.tempPassword);
                  } catch (error) {
                    console.error('Error resetting password:', error);
                  }
                }}
                sx={{ borderRadius: 2 }}
              >
                Reset Password
              </Button>
            </DialogActions>
          </>
        )}
      </Dialog>
      
      <Dialog open={!!tempPass} onClose={() => setTempPass('')} maxWidth="xs" fullWidth>
        <DialogTitle>Password Reset</DialogTitle>
        <DialogContent>
          <Typography variant="body1" paragraph>
            A temporary password has been generated:
          </Typography>
          <Box sx={{ 
            p: 2, 
            bgcolor: 'grey.100', 
            borderRadius: 1,
            fontFamily: 'monospace',
            textAlign: 'center',
            fontSize: '1.1rem',
            mb: 2
          }}>
            {tempPass}
          </Box>
          <Typography variant="body2" color="text.secondary">
            Please provide this to the teacher. They will be prompted to change it on first login.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setTempPass('')}>Close</Button>
          <Button 
            variant="contained" 
            color="primary" 
            onClick={() => {
              navigator.clipboard.writeText(tempPass);
              // You might want to add a snackbar notification here
            }}
          >
            Copy to Clipboard
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default AdminTeachers;
