import React, { useEffect, useState } from 'react';
import { Container, Typography, Grid, Paper, Stack, List, ListItem, ListItemText, Chip, TextField, Button, Divider, Checkbox, FormControlLabel } from '@mui/material';
import api from '../../api/client';

const TeacherDashboard = () => {
  const [classInfo, setClassInfo] = useState({ classNumber: null, students: [] });
  const [loading, setLoading] = useState(true);
  const [newStudent, setNewStudent] = useState({ name: '', email: '', password: '' });
  const [assignment, setAssignment] = useState({ subject: '', title: '', description: '', dueDate: '' });
  const [assignments, setAssignments] = useState([]);
  const [attendance, setAttendance] = useState({}); // studentId -> present bool

  useEffect(() => {
    (async () => {
      try {
        const { data } = await api.get('/teacher/me/class');
        setClassInfo(data);
        if (data?.classNumber) {
          await loadStudents();
          await loadAssignments();
          await loadAttendance();
        }
      } catch {
        setClassInfo({ classNumber: null, students: [] });
      } finally {
        setLoading(false);
      }
    })();
  }, []);

  const loadStudents = async () => {
    try {
      const { data } = await api.get('/teacher/students');
      setClassInfo((c) => ({ ...c, students: data || [] }));
    } catch {}
  };

  const loadAssignments = async () => {
    try {
      const { data } = await api.get('/teacher/assignments');
      setAssignments(data || []);
    } catch {}
  };

  const loadAttendance = async () => {
    try {
      const { data } = await api.get('/teacher/attendance');
      const map = {};
      (data || []).forEach((rec) => { map[rec.student?._id || rec.student] = !!rec.present; });
      setAttendance(map);
    } catch {}
  };

  const handleAddStudent = async () => {
    if (!newStudent.name || !newStudent.email || !newStudent.password) return;
    await api.post('/teacher/students/add', newStudent);
    setNewStudent({ name: '', email: '', password: '' });
    await loadStudents();
  };

  const handleCreateAssignment = async () => {
    if (!assignment.subject || !assignment.title) return;
    await api.post('/teacher/assignments', assignment);
    setAssignment({ subject: '', title: '', description: '', dueDate: '' });
    await loadAssignments();
  };

  const toggleAttendance = async (studentId, present) => {
    await api.post('/teacher/attendance/mark', { studentId, present });
    setAttendance((m) => ({ ...m, [studentId]: present }));
  };

  return (
    <Container sx={{ py: 6 }}>
      <Typography variant="h4" gutterBottom>Teacher Dashboard</Typography>
      {loading ? (
        <Typography>Loading...</Typography>
      ) : (
        <Grid container spacing={3}>
          <Grid item xs={12} md={5}>
            <Paper sx={{ p: 2 }}>
              <Stack spacing={1}>
                <Typography variant="h6">Assigned Class</Typography>
                <Typography color="text.secondary">{classInfo.classNumber ? `Class ${classInfo.classNumber}` : 'No class assigned'}</Typography>
              </Stack>
            </Paper>
            <Paper sx={{ p: 2, mt: 3 }}>
              <Typography variant="h6" sx={{ mb: 2 }}>Add Student</Typography>
              <Stack spacing={1}>
                <TextField size="small" label="Name" value={newStudent.name} onChange={(e)=>setNewStudent({...newStudent,name:e.target.value})} />
                <TextField size="small" label="Email" value={newStudent.email} onChange={(e)=>setNewStudent({...newStudent,email:e.target.value})} />
                <TextField size="small" label="Temp Password" type="password" value={newStudent.password} onChange={(e)=>setNewStudent({...newStudent,password:e.target.value})} />
                <Button variant="contained" onClick={handleAddStudent} disabled={!classInfo.classNumber}>Add</Button>
              </Stack>
            </Paper>
          </Grid>
          <Grid item xs={12} md={7}>
            <Paper sx={{ p: 2 }}>
              <Typography variant="h6" sx={{ mb: 1 }}>Students</Typography>
              {(!classInfo.students || classInfo.students.length === 0) ? (
                <Typography color="text.secondary">No students found</Typography>
              ) : (
                <List>
                  {classInfo.students.map((s) => (
                    <ListItem key={s._id} divider secondaryAction={<>
                      <FormControlLabel control={<Checkbox checked={!!attendance[s._id]} onChange={(e)=>toggleAttendance(s._id, e.target.checked)} />} label="Present" />
                      <Chip size="small" label={s.verified ? 'Verified' : 'Pending'} />
                    </>}> 
                      <ListItemText primary={s.name} secondary={`${s.email}${s.studentId ? '  ' + s.studentId : ''}`} />
                    </ListItem>
                  ))}
                </List>
              )}
            </Paper>
            <Paper sx={{ p: 2, mt: 3 }}>
              <Typography variant="h6" sx={{ mb: 2 }}>Create Assignment</Typography>
              <Grid container spacing={1}>
                <Grid item xs={12} sm={3}><TextField size="small" label="Subject" value={assignment.subject} onChange={(e)=>setAssignment({...assignment,subject:e.target.value})} fullWidth /></Grid>
                <Grid item xs={12} sm={4}><TextField size="small" label="Title" value={assignment.title} onChange={(e)=>setAssignment({...assignment,title:e.target.value})} fullWidth /></Grid>
                <Grid item xs={12} sm={5}><TextField size="small" label="Description" value={assignment.description} onChange={(e)=>setAssignment({...assignment,description:e.target.value})} fullWidth /></Grid>
                <Grid item xs={12} sm={6}><TextField size="small" type="date" label="Due Date" InputLabelProps={{ shrink: true }} value={assignment.dueDate} onChange={(e)=>setAssignment({...assignment,dueDate:e.target.value})} fullWidth /></Grid>
                <Grid item xs={12} sm={6}><Button variant="contained" onClick={handleCreateAssignment} sx={{ height: '40px' }}>Publish</Button></Grid>
              </Grid>
              <Divider sx={{ my: 2 }} />
              <Typography variant="subtitle1" sx={{ mb: 1 }}>Assignments</Typography>
              {assignments.length === 0 ? (
                <Typography color="text.secondary">No assignments yet</Typography>
              ) : (
                <List>
                  {assignments.map(a => (
                    <ListItem key={a._id} divider>
                      <ListItemText primary={`${a.subject}: ${a.title}`} secondary={`Due: ${a.dueDate ? new Date(a.dueDate).toLocaleDateString() : ''}`} />
                    </ListItem>
                  ))}
                </List>
              )}
            </Paper>
          </Grid>
        </Grid>
      )}
    </Container>
  );
};

export default TeacherDashboard;