import React, { useEffect, useState } from 'react';
import { Container, Typography, Paper, TextField, MenuItem, Button, Stack, Grid, List, ListItem, ListItemText } from '@mui/material';

const API = 'http://localhost:5000/api';

const AdminAddTeacher = () => {
  const [newTeacher, setNewTeacher] = useState({ name: '', email: '', password: '', classNumber: '', subject: '' });
  const [teachers, setTeachers] = useState([]);
  const [submitting, setSubmitting] = useState(false);

  useEffect(() => {
    fetchTeachers();
  }, []);

  const fetchTeachers = async () => {
    try {
      const res = await fetch(`${API}/admin/teachers`);
      const data = await res.json();
      setTeachers(Array.isArray(data) ? data : []);
    } catch {
      setTeachers([]);
    }
  };

  const handleAddTeacher = async () => {
    if (!newTeacher.name || !newTeacher.email || !newTeacher.password) return;
    setSubmitting(true);
    try {
      await fetch(`${API}/admin/add-teacher`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          name: newTeacher.name,
          email: newTeacher.email,
          password: newTeacher.password,
          classNumber: Number(newTeacher.classNumber) || undefined,
          subject: newTeacher.subject || undefined,
        })
      });
      setNewTeacher({ name: '', email: '', password: '', classNumber: '', subject: '' });
      await fetchTeachers();
      // Optional: toast/success indicator could be added
    } finally {
      setSubmitting(false);
    }
  };

  const handleDeleteTeacher = async (id) => {
    await fetch(`${API}/admin/delete-teacher`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ teacherId: id })
    });
    await fetchTeachers();
  };

  return (
    <Container sx={{ py: { xs: 6, md: 8 } }}>
      <Typography variant="h4" sx={{ mb: 2 }}>Add Teacher</Typography>
      <Grid container spacing={3}>
        <Grid item xs={12} md={5}>
          <Paper sx={{ p: 2 }}>
            <Stack spacing={1}>
              <TextField fullWidth size="small" label="Name" value={newTeacher.name} onChange={(e)=>setNewTeacher({...newTeacher,name:e.target.value})} />
              <TextField fullWidth size="small" label="Email" value={newTeacher.email} onChange={(e)=>setNewTeacher({...newTeacher,email:e.target.value})} />
              <TextField fullWidth size="small" label="Temp Password" type="password" value={newTeacher.password} onChange={(e)=>setNewTeacher({...newTeacher,password:e.target.value})} />
              <TextField fullWidth size="small" label="Subject" value={newTeacher.subject} onChange={(e)=>setNewTeacher({...newTeacher,subject:e.target.value})} select>
                <MenuItem value="">Select subject</MenuItem>
                <MenuItem value="Mathematics">Mathematics</MenuItem>
                <MenuItem value="Science">Science</MenuItem>
                <MenuItem value="English">English</MenuItem>
                <MenuItem value="Social Studies">Social Studies</MenuItem>
                <MenuItem value="Computer">Computer</MenuItem>
                <MenuItem value="Hindi">Hindi</MenuItem>
                <MenuItem value="Sanskrit">Sanskrit</MenuItem>
                <MenuItem value="Physics">Physics</MenuItem>
                <MenuItem value="Chemistry">Chemistry</MenuItem>
                <MenuItem value="Biology">Biology</MenuItem>
                <MenuItem value="Geography">Geography</MenuItem>
                <MenuItem value="History">History</MenuItem>
                <MenuItem value="Economics">Economics</MenuItem>
                <MenuItem value="Civics">Civics</MenuItem>
                <MenuItem value="Physical Education">Physical Education</MenuItem>
                <MenuItem value="Art">Art</MenuItem>
                <MenuItem value="Music">Music</MenuItem>
              </TextField>
              <TextField fullWidth size="small" label="Class Number (optional)" type="number" value={newTeacher.classNumber} onChange={(e)=>setNewTeacher({...newTeacher,classNumber:e.target.value})} />
              <Button variant="contained" onClick={handleAddTeacher} disabled={submitting}>Add Teacher</Button>
            </Stack>
          </Paper>
        </Grid>
        <Grid item xs={12} md={7}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="h6" sx={{ mb: 1 }}>Teachers</Typography>
            {teachers.length === 0 ? (
              <Typography color="text.secondary">No teachers yet</Typography>
            ) : (
              <List>
                {teachers.map(t => (
                  <ListItem key={t._id} divider secondaryAction={<Button color="error" onClick={()=>handleDeleteTeacher(t._id)}>Remove</Button>}>
                    <ListItemText primary={`${t.name} (${t.email})`} secondary={`Role: ${t.role}${t.classAssigned ? `  Class ${t.classAssigned}` : ''}${t.subject ? `  ${t.subject}` : ''}${t.verified ? '  Verified' : ''}`} />
                  </ListItem>
                ))}
              </List>
            )}
          </Paper>
        </Grid>
      </Grid>
    </Container>
  );
};

export default AdminAddTeacher;
